<template>
    <div class="wrapper">
        <div class="login_header"></div>
        <div class="form">
            <div class="account">
                <div class="left"></div>
                <input type="text" placeholder="">
            </div>
            <div class="password">
                <div class="password-content">
                    <div class="left code"></div>
                    <input type="text" placeholder="" class="codeInput">
                </div>
                <div class="getCode"></div>
            </div>
        </div>
        <div class="login_bg_red"></div>
        <div class="register"></div>
        <div class="login_border_red" @click="goLogin"></div>
    </div>
</template>
<script>
    export default {
        methods: {
            goLogin() {
                this.$router.push('./login')
            }
        }
    }

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
    @import '../../static/pubilc.scss';
    .wrapper {
        font-size: $fontSize;
        background-color: #f0f0f0;
        height: 100vh;
        max-width: $maxWidth;
        margin: 0 auto;
    }

    .form {
        margin-top: 1rem;
        background-color: #fff;
        text-align: left;
        & .account {
            padding: 0 0.8rem;
            border-bottom: 1px solid #ddd;
            display: flex;
            height: 2.5rem;
            line-height: 2.5rem;
            box-sizing: border-box;
        }
        & .password {
            padding: 0 0.8rem;
            display: flex;
            justify-content: space-between;
            height: 2.5rem;
            line-height: 2.5rem;
            box-sizing: border-box;
            & .password-content {
                display: flex;
            }
        }
        & .left {
            width: 4rem;
            color: #6c6c6c;
        }
        & .getCode {
            border-left: 1px solid #ccc;
            color: #f40;
            height: 1.1rem;
            line-height: 1.1rem;
            margin-top: 0.7rem;
            padding-left: 1rem;
        }
        & .code {
            width: 5rem;
        }
        & .codeInput {
            width: 5rem;
        }
        & input {
            border: 0 none;
            outline: none;
        }
        & input::-webkit-input-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input::-moz-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input::-moz-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input:-ms-input-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }

    }



    .register {
        padding: 0 0.8rem;
        color: #6c6c6c;
    }

</style>



