<template>
    <div class="wrapper">
        <div class="login_header"></div>
        <div class="form">
            <div class="account">
                <div class="left"></div>
                <input type="text" placeholder="//">
            </div>
            <div class="password">
                <div class="left"></div>
                <input type="text" placeholder="">
            </div>
        </div>
        <div class="login_bg_red"></div>
        <div class="other">
            <div class="note" @click="goNoteLogin"></div>
            <div class="register"></div>
        </div>
        <div class="login_border_red"></div>
    </div>
</template>
<script>
    export default {
        methods: {
            goNoteLogin() {
                this.$router.push('./noteLogin')
            }
        }
    }

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
    @import '../../static/pubilc.scss';
    .wrapper {
        font-size: $fontSize;
        background-color: #f0f0f0;
        height: 100vh;
        max-width: $maxWidth;
        margin: 0 auto;
    }

    .form {
        margin-top: 1rem;
        background-color: #fff;
        text-align: left;
        & .account {
            padding: 0 0.8rem;
            border-bottom: 1px solid #ddd;
            display: flex;
            height: 2.5rem;
            line-height: 2.5rem;
            box-sizing: border-box;
        }
        & .password {
            padding: 0 0.8rem;
            display: flex;
            height: 2.5rem;
            line-height: 2.5rem;
            box-sizing: border-box;
        }
        & .left {
            width: 4rem;
            color: #6c6c6c;
        }
        & input {
            border: 0 none;
            outline: none;
        }
        & input::-webkit-input-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input::-moz-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input::-moz-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }
        & input:-ms-input-placeholder {
            color: #ccc;
            font-size: $fontSize;
        }

    }



    .other {
        display: flex;
        padding: 0 0.8rem;
        justify-content: space-between;
        color: #f40;
    }

</style>



